import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { PreRegisterPatient } from '../../masters/pre-register-patient';
import { PreRegisterPatientService } from '../../service/pre-register-patient.service';
import { PrefixMasterService } from '../../service/prefix-master.service';
import { routerTransition } from '../../../router.animations';
import { PatientAddress } from '../../../patient/master/patient_address';
import { LookupEntity } from '../../../master/lookup-module/masters/lookup-entity';
import { PrefixMaster } from '../../../master/unit-master/masters/PrefixMaster';
import { Branch } from '../../../master/service-center/masters/branch';
import { Department } from '../../../master/department/masters/department';
import { LookupService } from '../../../master/lookup-module/services/lookup.service';
import { PatientAddressService } from '../../../master/country-category/services/patient-address.service';
import { DepartmentService } from '../../../master/department/services/department.service';
import { BranchServiceService } from '../../../master/service-center/services/branch-service.service';

@Component({
  selector: 'app-add-pre-register-patient',
  templateUrl: './add-pre-register-patient.component.html',
  styleUrls: ['./add-pre-register-patient.component.css'],
  animations: [routerTransition()],
})
export class AddPreRegisterPatientComponent implements OnInit {
  preRegisterPatient: PreRegisterPatient = new PreRegisterPatient();
  submitted = false;
  preRegistrationForm =new  FormGroup({});
  lookupEntityList: Array<LookupEntity> = new Array<LookupEntity>();
  prefixList: Array<PrefixMaster> = new Array<PrefixMaster>();
  branchList: Array<Branch> = new Array<Branch>();
  departmentList: Array<Department> = new Array<Department>();
  patientAddressList: Array<PatientAddress> = [];
  constructor(private PreRegistrationService: PreRegisterPatientService,private lookupServic: LookupService,
  private prefixMasterService: PrefixMasterService,private patientAddressService: PatientAddressService,
  private branchService: BranchServiceService,private departmentService: DepartmentService,
  private router: Router,private fb: FormBuilder) { }

  ngOnInit(): void {
	this.lookupServic.getAllLookupEntities().subscribe(data =>{
		this.lookupEntityList = data;
	});
	this.prefixMasterService.getPrefixMasterList().subscribe(data =>{
		this.prefixList = data;
		});
	 this.branchService.getBranch().subscribe(data =>{
		this.branchList = data;
		console.log(this.branchList);
		});
 
 this.departmentService.getAllDepartments().subscribe(data =>{
		this.departmentList = data;
		console.log(this.departmentList)
		});
		this.patientAddressService.getPatientAddressList().subscribe(data =>{
			this.patientAddressList = data;
			console.log(data);
		});
	
	this.preRegistrationForm = this.fb.group({
	  preRegistrationId:new FormControl(null),
	  preRegnType :new FormControl(null),
	  preRegistrationNo:new FormControl(null),
	  queueDate: new FormControl(null),
	  firstName : new FormControl(null),
	  middleName : new FormControl(null),
	  lastName : new FormControl(null),
	  patientPrefix : new FormControl(null),
	  gender  : new FormControl(null),
	  dob  : new FormControl(null),
	  age  : new FormControl(null),
	  patientAddress :new FormControl(null),
	  telephoneNo : new FormControl(null),
	  language    :new FormControl(null),
	  remarks : new FormControl(null),
	  reasonforEnquiry : new FormControl(null),
	  paymentMode  : new FormControl(null),
	  hdyhau : new FormControl(null),
	  branch :new FormControl(null),
	  department : new FormControl(null),
	});
  }


PreRegistration(): void {
    this.submitted = false;
    this.preRegisterPatient= new PreRegisterPatient();
  }

  save() {
	
	this.preRegisterPatient = this.preRegistrationForm.value;
	this.preRegisterPatient.active = true;
	console.log(this.preRegisterPatient)
    this.PreRegistrationService.addPreRegistration(this.preRegisterPatient).subscribe(data => {
      console.log(data)
      this.preRegisterPatient = new PreRegisterPatient();
      this.gotoList();
    }, 
    error => console.log(error));
  }

  onSubmit() {
    console.log(this.preRegistrationForm.value)
	
    this.save();    
  }

  gotoList() {
    this.router.navigate(['/preRegistration/pre-register-patient-list']);
  }

  }