import { Country } from "../../master/country-category/masters/country";
import { District } from "../../master/country-category/masters/district";
import { LookupEntity } from "../../master/lookup-module/masters/lookup-entity";
import { State } from "../../master/country-category/masters/state";


export class PatientAddress {
	

	  addressId : number ;
 	  address: String; // --->House No
	  postOffice :String ; // --->Locality PO
	  policeStation :String ;// --->Plice sation
	  state : State = new State();
 	  pin :String ;
	  phone1 :String ;
	  email :String ;
	  contactPerson:String;
	  relation : LookupEntity  = new LookupEntity();
	  active:  Boolean  = false;
	  isRural : boolean;
	  isUrban: boolean ;
	  country: Country = new Country();
	  district :  District = new District();
	
}