import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ServiceCenter } from '../masters/service-center';


@Injectable({
  providedIn: 'root'
})
export class BranchServiceService {
	baseUrl = "http://localhost:8080/springboot/api/v1";
  constructor(private http: HttpClient) { }
	/* 
	************************ The ServiceCenter api's goes here. ************************************
	*/
	
	getServiceCenter(): Observable<any>{
		return this.http.get(`${this.baseUrl}/serviceCenter`);
	}
	
	updateServiceCenter(serviceCenterId: number,serviceCenter: ServiceCenter):any{
		return this.http.put(`${this.baseUrl}/serviceCenter/${serviceCenterId}`,serviceCenter);
	}
	
	createServiceCenter(serviceCenter: ServiceCenter){
		return this.http.post(`${this.baseUrl}/serviceCenter`,serviceCenter);
	}
	/* 
	************************ The Branch api's goes here. *******************************************
	*/
	getBranch(): any{
		return this.http.get(`${this.baseUrl}/branch`);
	}
	/* 
	************************ The Block api's goes here. ********************************************
	*/
	getBlock(): any{
		return this.http.get(`${this.baseUrl}/block`);
		
	}
	/* 
	************************ The Floor api's goes here. *********************************************
	*/
	
	getFloor(): any{
		return this.http.get(`${this.baseUrl}/floor`);
	}
}