import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { PreRegisterPatient } from '../masters/pre-register-patient';


@Injectable({
  providedIn: 'root'
})
export class PreRegisterPatientService {
private baseUrl = 'http://localhost:8080/springboot/api/v1';
  

  constructor(private http: HttpClient) { }
/*
 getPreRegistration(id: number): any {
    return this.http.get(`${this.baseUrl}/preregistrations/${id}`);
  }
*/
addPreRegistration(preRegisterPatient: PreRegisterPatient): any {
    return this.http.post(`${this.baseUrl}/preRegisteredPatient/add`, preRegisterPatient);
  }

getPreRegistrationList(): any {
    return this.http.get(`${this.baseUrl}/preRegisteredPatient/get`);
  }


}