import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Unit } from '../masters/unit';

@Injectable({
  providedIn: 'root'
})
export class UnitMasterService {

  constructor(private http: HttpClient) { }
	baseUrl = "http://localhost:8080/springboot/api/v1";
	
	getUnit():any{
		return this.http.get(`${this.baseUrl}/units`)
	}
	
	createUnit(unit):any{
		return this.http.post(`${this.baseUrl}/units`,unit);
	}
	updateUnit(unitId: number, unit: Unit):any{
		return this.http.put(`${this.baseUrl}/units/${unitId}`,unit)
	}
	
	
	/* Unit-Employee Mapping goes her ************************************************/
	getUnitEmployeeMapping(): any{
		return this.http.get(`${this.baseUrl}/unitEmployees`)
	}
	
	getOnlyRemainingEmployee(departmentId: number,unitId : number):any{
		return this.http.get(`${this.baseUrl}/unitEmployeeOnlyEmployees/${departmentId}/${unitId}`)
	}
}
