import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'search'
})
export class SearchPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    if (!args) {
      return value;
    }
    return value.filter((val) => {
		args = args.toLowerCase();
      let rVal = (val.lastName.toLocaleLowerCase().includes(args) || (val.firstName.toLowerCase().includes(args) || (val.mobile.toString().toLowerCase().includes(args))|| val.onlineMedia.toLowerCase().includes(args)||val.patientAddress.address.toLowerCase().includes(args)||val.patientAddress.state.stateName.toLowerCase().includes(args)|| val.patientAddress.state.country.countryName.toLowerCase().includes(args) ));
      return rVal;
    })

  }

}
