import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { LookupCategory } from '../masters/lookup-category';




@Injectable({
  providedIn: 'root'
})
export class LookupService {
	baseUrl = 'http://localhost:8080/springboot/api/v1';
  constructor(private http: HttpClient) { }

	getLookupCategoryList():any
	{
		return this.http.get(`${this.baseUrl}/lookupcategory`);
    }
	createLookupCategory(LookupCategory : Object): any{
		return this.http.post(`${this.baseUrl}/lookupcategory`,LookupCategory);
	}
	updateLookupCategory(categoryId: number, category: LookupCategory){
		return this.http.put(`${this.baseUrl}/lookupcategory/${categoryId}`,category);
	}
	deleteLookupCategory(categoryId: number){
		return this.http.delete(`${this.baseUrl}/lookupcategory/${categoryId}`);
	}
	getLookupCategoryById(categoryId : number): any
	{
		return this.http.get(`${this.baseUrl}/lookupcategory/${categoryId}`);
	}
	

//LookupEntity Functions here 
	getLookupEntityList(id: number): any
	{
		return this.http.get(`${this.baseUrl}/lookupEntity/getByCategoryId/${id}`);
	}
	getAllLookupEntities():any{
		return this.http.get(`${this.baseUrl}/lookupEntity/get`)
	}
	
	createLookupEntity(entity: any): any
	{
		console.warn(entity);
		return this.http.post(`${this.baseUrl}/lookupEntity/add`, entity);
  	}
	updateLookupEntity(lookupId: number, value: any): Observable<Object>
	{
		console.log(value);
    	return this.http.put(`${this.baseUrl}/lookupEntity/update/${lookupId}`, value);
 	}
	deleteLookupEntity(lookupId): any{
		return this.http.delete(`${this.baseUrl}/lookupEntity/delete/${lookupId}`)
	}
}