import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PatientService {

baseUrl = "http://localhost:8080/springboot/api/v1/patient"

 constructor(private http: HttpClient) { }

getPatientList(): any{
	return this.http.get(`${this.baseUrl}`);
}

getPatient(id: number): any{
	return  this.http.get(`${this.baseUrl}/${id}`)
}

deletePatient(id: number){
	return this.http.delete(`${this.baseUrl}/${id}`)
}

createPatient(value : object):any{
	return this.http.post(`${this.baseUrl}`, value)
}
updatePatient(mrn: string , value : object): Observable<Object>{
	return this.http.put(`${this.baseUrl}/${mrn}`,value);
}
}
