import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ScheduleDoctorService {
private baseUrl = 'http://localhost:8080/springboot/api/v1';
  

  constructor(private http: HttpClient) { }

 getScheduleDoctorList(): any {
    return this.http.get(`${this.baseUrl}/scheduleDoctor/get`);
  }
   getScheduleDoctorsByDoctorId(doctorId): any {
    return this.http.get(`${this.baseUrl}/scheduleDoctor/getById/${doctorId}`);
  }
   createScheduleDoctor(scheduleDoctor): any {
    return this.http.post(`${this.baseUrl}/scheduleDoctor/add`,scheduleDoctor);
  }



}