
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { PreRegisterAppointment } from '../masters/pre-register-appointment';

@Injectable({
  providedIn: 'root'
})
export class PreRegisteredAppointmentService {
private baseUrl = 'http://localhost:8080/springboot/api/v1';
  

  constructor(private http: HttpClient) { }
/*
 getPreRegistration(id: number): any {
    return this.http.get(`${this.baseUrl}/preregistrations/${id}`);
  }
*/
addPreRegisteredAppointment(preRegisterAppointment: PreRegisterAppointment): any {
    return this.http.post(`${this.baseUrl}/preRegisteredAppointment/add`, preRegisterAppointment);
  }

getPreRegisteredAppointmentList(): any {
    return this.http.get(`${this.baseUrl}/preRegisteredAppointment/get`);
  }


}