import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { PatientAddress } from '../../../patient/master/patient_address';
import { Country } from '../masters/country';
import { District } from '../masters/district';
import { State } from '../masters/state';

@Injectable({
  providedIn: 'root'
})
export class PatientAddressService {
  baseUrl = "http://localhost:8080/springboot/api/v1";
  constructor(private http: HttpClient) { }

	createPatientAddress(patientAddress: PatientAddress)
	{
		return this.http.post(`${this.baseUrl}/patientAddress/add`,patientAddress)
	}
	
	getPatientAddressList() : any
	{
		return this.http.get(`${this.baseUrl}/patientAddress/get`)
	}
	//Country's Function starts here
	
   	getCountries():any
	{
		return this.http.get(`${this.baseUrl}/country`)
	}
	createCountry(country: Country){
		return this.http.post(`${this.baseUrl}/country`,country)
	}
	deleteCountry(countryId: number){
		return this.http.delete(`${this.baseUrl}/country/${countryId}`)
	}
	getCountryById(countryId: number):any{
		return this.http.get(`${this.baseUrl}/country/${countryId}`)
	}
	updateCountry(countryId: number, country: Country): any{
		return  this.http.put(`${this.baseUrl}/country/${countryId}`,country)
	}
	
	
	//State's Functions start here
	getStates():any
	{
		return this.http.get(`${this.baseUrl}/state`)
	}
	getStatesById(stateId: number):any{
		return this.http.get(`${this.baseUrl}/state/${stateId}`)
	}
	createState(state: State){
		return this.http.post(`${this.baseUrl}/state`,state)
	}
	updateState(stateId: number,state : State): any{
		return this.http.put(`${this.baseUrl}/state/${stateId}`,state);
	}
	deleteState(stateId: number){
		return this.http.delete(`${this.baseUrl}/state/${stateId}`)
	}
	getStateByCountry(countryId: number):any{
		return this.http.get(`${this.baseUrl}/states/${countryId}`)
	}
	
	//District section goes here
	
	getDistricts():any
	{
		return this.http.get(`${this.baseUrl}/district`)
	}
	getDistrictById(districtId: number):any{
		return this.http.get(`${this.baseUrl}/district/${districtId}`)
	}
	createDistrict(district: District){
		return this.http.post(`${this.baseUrl}/district`,district)
	}
	updateDistrict(districtId: number,district : District): any{
		return this.http.put(`${this.baseUrl}/district/${districtId}`,district);
	}
	deleteDistrict(districtId: number){
		return this.http.delete(`${this.baseUrl}/district/${districtId}`);
	}
	getDistrictByState(stateId: number ){
		return this.http.get(`${this.baseUrl}/districts/${stateId}`)
	}
}
