import { HttpClient } from '@angular/common/http';
import { Component, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { routerTransition } from '../../../router.animations';
import { Patient } from '../../master/patient';
import { PatientService } from '../../service/patient.service';


@Component({
  selector: 'app-patient-list',
  templateUrl: './patient-list.component.html',
  styleUrls: ['./patient-list.component.css'],
	animations: [routerTransition()]
  })

export class PatientListComponent implements OnInit {
	patientList : Patient;
	name = "";
	page = 1;
	pageSize = 5;
	len : number;
  constructor(private http: HttpClient,private patientService: PatientService,private router: Router) { }

  ngOnInit(): void {
	this.getPatient();
  }

	getPatient(): any{
		this.patientService.getPatientList().subscribe(data => {
		console.log(data),this.patientList = data, this.len = data.length
	});
	}
	
	deletePatient(id){
		this.patientService.deletePatient(id).subscribe(data => {
			console.log(data);
		 })
	this.getPatient();
	}
	updatePatient(patient){
		
		this.router.navigate(['/registration/createpatient'],{state:patient});
	}
	
	}
	
	
