import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { routerTransition } from '../../../router.animations';
import { PreRegisterPatient } from '../../masters/pre-register-patient';
import { PreRegisterPatientService } from '../../service/pre-register-patient.service';

@Component({
  selector: 'app-pre-register-patient-list',
  templateUrl: './pre-register-patient-list.component.html',
  styleUrls: ['./pre-register-patient-list.component.css'],
  animations: [routerTransition()],
})
export class PreRegisterPatientListComponent implements OnInit {

	len: number;
	page: number =1;
	pageSize: number =5;
	preregistrationList: Array<PreRegisterPatient>;
  constructor(private preregistrationService: PreRegisterPatientService,private route:ActivatedRoute,
    private router: Router) { }

  ngOnInit(): void {
	this.preregistrationService.getPreRegistrationList().subscribe(data =>{
		this.preregistrationList = data;
		this.len = data.length;
		console.log(data);
	});
  }
gotoPreRegistrationAppointment(preregistration){
	let data = JSON.stringify(preregistration)
	sessionStorage.setItem('preRegisterPatient', data)
    this.router.navigate(['/preRegistration/pre-registered-appointment'])
  }


}
