import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CreatePatientComponent } from './components/create-patient/create-patient.component';
import { EmergencyRegistrationComponent } from './components/emergency-registration/emergency-registration.component';
import { PatientListComponent } from './components/patient-list/patient-list.component';

import { Patient } from './master/patient';
;
import { PatientComponent } from './patient.component';

const routes: Routes = [
	
	{path: '' , component: PatientComponent},
	{path: 'createpatient' , component: CreatePatientComponent},
	{path: 'patientlist', component: PatientListComponent},
	{path: 'emergencyregistration', component: EmergencyRegistrationComponent},
	
	];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PatientRoutingModule { }
