import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import {Account} from '../master/account';

@Injectable({
  providedIn: 'root'
})
export class AccountService {
	baseUrl = "http://localhost:8080/springboot/api/v1";
	constructor(private http: HttpClient){}
	getAccountList():Observable<any>{
		return this.http.get(`${this.baseUrl}/account/get`);
	}
	createAccount(account : Account): any{
		return this.http.post(`${this.baseUrl}/account/add`,account);
	}
}