import { Component, OnInit } from '@angular/core';
import { routerTransition } from '../../../router.animations';
import { PreRegisterAppointment } from '../../masters/pre-register-appointment';
import { PreRegisteredAppointmentService } from '../../service/pre-registered-appointment.service';

@Component({
  selector: 'app-pre-register-appointment-list',
  templateUrl: './pre-register-appointment-list.component.html',
  styleUrls: ['./pre-register-appointment-list.component.css'],
  animations: [routerTransition()],
})
export class PreRegisterAppointmentListComponent implements OnInit {
	
	preRegisteredAppointmentList: Array<PreRegisterAppointment> = [];
	preRegisterAppointment: PreRegisterAppointment =new PreRegisterAppointment;
	len : number; 
	page: number =1 ;
	pageSize: number =5;
  constructor(private preRegisterAppointmentService: PreRegisteredAppointmentService) { }

  ngOnInit(): void {
	this.reloadData();
  }
	reloadData(){
		this.preRegisterAppointmentService.getPreRegisteredAppointmentList().subscribe(data => {
			console.log(data);
			this.preRegisteredAppointmentList = data;
			this.len = data;
		})
	}
}
