import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AddPreRegisterPatientComponent } from './components/add-pre-register-patient/add-pre-register-patient.component';
import { PreRegisterAppointmentListComponent } from './components/pre-register-appointment-list/pre-register-appointment-list.component';
import { PreRegisterAppointmentComponent } from './components/pre-register-appointment/pre-register-appointment.component';
import { PreRegisterPatientListComponent } from './components/pre-register-patient-list/pre-register-patient-list.component';
import { PreRegisterPatientComponent } from './pre-register-patient.component';


const routes: Routes = [
	{path: '', component: PreRegisterPatientComponent},
	{path: 'pre-register-patient-list', component: PreRegisterPatientListComponent},
	{path: 'add-pre-register-patient', component: AddPreRegisterPatientComponent},
	{path: 'pre-registered-appointment', component: PreRegisterAppointmentComponent},
	{path: 'pre-registered-appointment-list', component: PreRegisterAppointmentListComponent}
	
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PreRegisterPatientRoutingModule { }
