import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Department } from '../masters/department';

@Injectable({
  providedIn: 'root'
})
export class DepartmentService  {

	
  baseUrl = "http://localhost:8080/springboot/api/v1/department";

  constructor(private http:HttpClient) { }

  getAllDepartments() : Observable<any>
  {
	return this.http.get(`${this.baseUrl}`);
  }

  updateDepartment(id: number, value: any): Observable<Object> 
  {
    return this.http.put(`${this.baseUrl}/${id}`, value);
  }
	createDepartments(departments : Department): any{
		return this.http.post(`${this.baseUrl}`,departments);
	}
}
