	createPatient(value : object){
	return this.http.post(`${this.baseUrl}`, value)
}

	newPatient(): void {
    this.submitted = false;
    this.patient = new Patient();
  }

  save(){
	this.patient = this.regform.value;
	console.log(this.patient)
	
	 this.patientService
    .createPatient(this.patient).subscribe(data => {
      console.log(data)
      this.patient = new Patient();
      this.gotoList();
    }, 
    error => console.log(error));
  }

 RegisterPatient(){
    this.submitted = true;
    this.save();    
  }

  gotoList() {
    this.router.navigate(['']);
  }

agecalculator()
	{
		var dobt : Date  = new Date(this.regform.value.dob) ;
		console.log(dobt);
		console.log(Date.now());
		console.log(dobt.getTime());
		var ageyear = Math.floor((Date.now()-dobt.getTime())/(1000 * 3600 * 24));
		if (ageyear <= 30)
		{
			(<FormControl>this.regform.controls['age']).setValue(ageyear);
			(<FormControl>this.regform.controls['ageUnit']).setValue('Days');
		}
		else if(ageyear < 365)
		{
			(<FormControl>this.regform.controls['age']).setValue(Math.floor(ageyear/30));
			(<FormControl>this.regform.controls['ageUnit']).setValue('Months');
		}
		else
		{
		(<FormControl>this.regform.controls['age']).setValue(Math.floor(ageyear/365));
		(<FormControl>this.regform.controls['ageUnit']).setValue('Years');
		}
	}
	ageformat()
	{
		console.log(this.regform.get('ageUnit').value)
		if (this.regform.get('ageUnit').value == "Years")
		{
			var dobt : Date  = new Date(this.regform.value.dob) ;
			var ageyear = Math.floor((Date.now()-dobt.getTime())/(1000 * 3600 * 24)/365);
			(<FormControl>this.regform.controls['age']).setValue(ageyear);
		}
		else if (this.regform.get('ageUnit').value == "Months")
		{
			var dobt : Date  = new Date(this.regform.value.dob) ;
			var ageyear = Math.floor((Date.now()-dobt.getTime())/(1000 * 3600 * 24)/12);
			(<FormControl>this.regform.controls['age']).setValue(ageyear);
		}
		else if (this.regform.get('ageUnit').value == "Days")
		{
			var dobt : Date  = new Date(this.regform.value.dob) ;
			var ageyear = Math.floor((Date.now()-dobt.getTime())/(1000 * 3600 * 24));
			(<FormControl>this.regform.controls['age']).setValue(ageyear);
	}
	} 
changeId(){
	this.id = this.regform.get('nothavingid').value;
	
}


